/**
 * 图标
 */
.icon{
	display: inline-block;
	background: url(../images/redm_sprites.png) no-repeat;
	background-size: 4rem 4rem;
}

.icon-share{
	display: inline-block;
	width: .18rem;
	height: .19rem;
	background: url(../images/share.png) no-repeat left top;
	background-size: contain;
}

.icon-look{
	display: inline-block;
	width: .29rem;
	height: .18rem;
	background: url(../images/look.png) no-repeat left top;
	background-size: contain;
}

.icon-like{
	display: inline-block;
	width: .21rem;
	height: .18rem;
	background: url(../images/like-hot.png) no-repeat left top;
	background-size: contain;
}

.icon-arrow-right{
	display: inline-block;
	width: .09rem;
	height: .17rem;
	background: url(../images/arrow-right.png) no-repeat left top;
	background-size: contain;
}

.icon-like-active{
	display: inline-block;
	width: .21rem;
	height: .18rem;
	background: url(../images/like-hot-active.png) no-repeat left top;
	background-size: contain;
}

.icon-hot-arrow-right{
	display: inline-block;
	width: .12rem;
	height: .21rem;
	background: url(../images/hot-list-arrow-right.png) no-repeat left top;
	background-size: contain;
}

.icon-tick{
	display: inline-block;
	background: url(../images/redm_sprites.png) no-repeat;
	background-size: 4rem 4rem;
}

.icon-sort{
	&.up .sort-up{
		background: url(../images/filter-up-active.png) no-repeat center top;
		background-size: 100% 100%;
	}

	&.down .sort-down{
		background: url(../images/filter-down-active.png) no-repeat center top;
		background-size: 100% 100%;
	}

	.sort-up{
		display: block;
		width: .15rem;
		height: .08rem;
		margin-bottom: .02rem;
		background: url(../images/filter-up.png) no-repeat center top;
		background-size: 100% 100%;
	}
	.sort-down{
		display: block;
		width: .15rem;
		height: .08rem;
		background: url(../images/filter-down.png) no-repeat center top;
		background-size: 100% 100%;
	}
}

.icon-filter{
	display: inline-block;
	width: .2rem;
    height: .26rem;
    background: url(../images/filter.png) no-repeat center top;
    background-size: .2rem auto;

    &.active{
    	background: url(../images/filter-active.png) no-repeat center top;
    	background-size: .2rem auto;
    }
}

.icon-new-tag{
	display: inline-block;
	width: .54rem;
	height: .56rem;
	background: url(../images/new-tag.png) no-repeat left top;
	background-size: 100% 100%;
}

.icon-clock{
	display: inline-block;
	width: .19rem;
	height: .2rem;
	background: url(../images/clock.png) no-repeat;
	background-size: 100% 100%;
}

.icon-two-arrow{
	width: .3rem;
	height: .13rem;
	background: url(../images/two_arrow.png) no-repeat left top;
	background-size: contain;
	display: inline-block;
}

.icon-618{
	position: absolute;
	width: .64rem;
	height: .85rem;
	background: url(../images/618.png) no-repeat left top;
	background-size: 100% 100%;
	top: 0;
	right: .17rem;
}

.icon-tris{
	position: relative;
	display: inline-block;
	text-align: left;

	&:before{
		content: "\20";
		position: absolute;
		top: 0;
		border-width: 0.09rem;
		border-top-width: 0;
		border-color: transparent transparent #666666 transparent;
		border-style: dashed dashed solid dashed
	}

	&:after{
		position: absolute;
		content: "\20";
		bottom: 0;
		border-width: 0.09rem;
		border-bottom-width: 0;
		border-color: #666666 transparent transparent transparent;
		border-style: solid dashed dashed dashed
	}
}

.icon-tick-black{
	display: inline-block;
	width: .2rem;
	height: .2rem;
	background: url(../images/tick.png);
	background-size: 100% 100%;
}

.icon-home{
	display: inline-block;
	width: .36rem;
	height: .28rem;
	background: url(../images/back-home.png) no-repeat left top;
	background-size: 100% 100%;
}
