/**
 * 1px 边框
 */
@mixin border1px-common() {
	position: absolute;
	content: '';
	z-index: 10;
}

.border-bottom{
	position: relative;

	&::after{
		bottom: -1px;
		left: 0;
		height: 1px;
		width: 100%;
		@include scaleY(.5);
		@include border1px-common();
		border-bottom-width: 1px;
		border-bottom-style: solid;
	}
}
.border-top{
	position: relative;

	&::before{
		top: -1px;
		left: 0;
		height: 1px;
		width: 100%;
		@include scaleY(.5);
		@include border1px-common();
		border-top-width: 1px;
		border-top-style: solid;
	}
}
.border-right{
	position: relative;

	&::after{
		@include border1px-common();
		border-right-width: 1px;
		border-right-style: solid;
		right: 0;
		width: 1px;
		top: 0;
		height: 100%;
		@include scaleX(.5);
	}
}
.border-left{
	position: relative;

	&::after{
		left: 0;
		top: 0;
		width: 1px;
		height: 100%;
		@include scaleX(.5);
		@include border1px-common();
		border-left-width: 1px;
		border-left-style: solid;
	}
}

/**
 * 边框颜色
 */
